.description {
    text-align: center;
    padding-top: 10px;
    margin-top: 10px;
  }
  
  .profile {
    width: 318px;
  
    background-color: rgb(233, 241, 237);
    border: 1px solid rgb(22, 22, 22);
  }
  
  .avatar {
    width: 250px;
    border-radius: 50%;
    background: white;
  }
  
  .name {
    font-size: 24px;
    font-weight: 600;
  }
  
  .tag {
    color: gray;
    font-weight: 600;
  }
  
  .location {
    color: rgb(75, 70, 70);
    font-weight: 600;
  }
  
  .stats {
    display: flex;
    justify-content: space-between;
    width: 318px;
  
    padding: 0px;
    margin: 0;
    list-style: none;
  }
  
  .statsInfo {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 29px;
    background-color: rgb(91, 97, 180);
    border: 1px solid rgb(22, 22, 22);
  }
  
  .statsInfo:hover {
    background-color: teal;
  }
  
  .label {
    color: rgb(56, 51, 51);
  }
  
  .quantity {
    font-weight: 700;
  }